.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.social
{
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;

  color: black;
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  /* color: #9a9b9c; */
  color: black;
}
.social i:hover
{
  /* color: #ffffff; */
  color: black;
}